import { Component, Vue } from "vue-property-decorator";
import Util from "../Core/Util";
import { IBaseNode } from "../Log";

@Component({
    props: {
        root: Object
    }
})
export default class SOQLTab extends Vue {
    private root?: IBaseNode;
    private filter: string = "";
    private grouped: boolean = false;

    get regex() {
        return this.filter ? new RegExp("(" + Util.escapeRegex(this.filter) + ")", "i") : null;
    }
}
