export default class Viewport {
    public Left: number;
    public Right: number;
    public Scale: number;

    private minLeft: number;
    private maxRight: number;

    public constructor(left: number, right: number, scale: number) {
        this.Scale = scale;
        this.Left = this.minLeft = left * scale;
        this.Right = this.maxRight = right * scale;
    }

    public GetWidth(): number { return this.Right - this.Left; }

    public Translate(delta: number): void {
        const clampedDelta: number = Math.min(Math.max(delta, this.minLeft - this.Left), this.maxRight - this.Right);
        this.Left += clampedDelta;
        this.Right += clampedDelta;
    }

    public Zoom(scale: number, center: number): void {
        const centerX: number = this.Left + this.GetWidth() * center;
        const newWidth = this.GetWidth() * scale;
        this.Right = Math.min(this.maxRight, centerX + (newWidth * (1 - center)));
        this.Left = Math.max(this.minLeft, centerX - (newWidth * center));
    }

    public ScreenRatioToUnscaledPosition(ratio: number) {
        return (this.Left + this.GetWidth() * ratio) / this.Scale;
    }
}
